function bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    var LocationData=[];
    var TagData=[]
    if (filtername == "Location") {
       var params  = `reqForm=`
    DataService.GetLocationByCompanyId(params)
        .then(function (response) {
            var json = response.data;
            if (filtername == "Location") {
                let obj={}
                JSON.parse(response.data).data.forEach(element => {
                    obj={
                    name:element.LOCATION_NAME,
                    value:element.COMPANY_LOCATION_ID
                    }
                    LocationData.push(obj);
                });

                vm.leftSearchFields[0].listOptions =LocationData;
            }
        });
    }
     

     
}
export default { buttons,leftsearchSchema,bindleftCommonSearchdropdown,

};